<template>
    <div class="detail-presell">
        <div>
            <div v-if="list.isin===1">
                <van-swipe class="swiper" :autoplay="0" :show-indicators=false indicator-color="white">
                    <van-swipe-item><img class="top-img" :src="list.imgurl" alt=""></van-swipe-item>
                </van-swipe>
                <div class="presell-title">
                    <span class="star" v-if="list.singername">{{list.singername}}</span>
                    <span class="t">{{list.title}}</span>
                </div>
                <p class="blue-tips">未成年人请在监护人同意下使用平台服务</p>
                <p class="support-progress">完成进度：{{nowNum.toFixed(2)}} {{supportType}} / {{aimNum.toFixed(2)}}
                    {{supportType}}</p>
                <div class="van-p">
                    <my-progress :progresssVal="precent.toFixed(2)*1"></my-progress>
                    <!--<van-progress color="#F50789" :percentage=100></van-progress>-->
                </div>
                <div class="support-time"><span>{{list.activityUserCount}}人支持</span>
                    <span v-if="typeof(list.endtime)==='number'">截止时间：{{$formatTime(new Date(list.endtime))}}</span>
                    <span v-if="typeof(list.endtime)==='string'">{{list.endtime}}</span>
                </div>
                <div class="follow-sth">
                    <div class="follow-head"
                         :style="{background:'url('+ list.userurl+')center no-repeat','background-size':'cover'}"></div>
                    <div class="follow-nickname">{{list.username}}</div>
                    <div class="follow-btn" :class="{'gray-font':list.isconcern===1}" @click="$isApp">
                        {{list.isconcern===1?'已关注':'+关注'}}
                    </div>
                    <div class="fans-num">粉丝：{{list.userfansnum}}</div>
                </div>
                <!--<img class="top-img" :src="list.imgurl" alt="">-->
                <div class="img-box">
                    <img class="showImg" :src="item" v-for="item in list.detailimgurl" alt="">
                </div>
                <!--<van-cell class="should-know" title="预售拨款须知" is-link/>-->
                <van-cell title="贡献榜" is-link :to="{path:'/supportList',query:{id:id,type:supportType}}"></van-cell>
                <ul class="contribution-list">
                    <li class="No" v-for="(item,index) in supportList.list" :key="index">
                        <div class="contribution-No">{{index+1}}</div>
                        <div class="contribution-info">
                            <img class="contribution-head" :src="item.userHeadUrl" alt="">
                            <p class="contribution-name ">{{item.userName}}</p>
                            <div class="contribution-value">{{item.contribution.toFixed(2)}}{{supportType}}</div>
                        </div>
                    </li>
                </ul>
                <div class="footer-btn">
                    <van-button class="opreate-btn" type="primary" size="large" @click="$isApp"
                                :class="{grayColor:changeGray}">{{buttonText}}
                    </van-button>
                </div>
            </div>
        </div>
        <div v-if="list.isin===0"><img class="default-img" src="../../assets/images/no-goods.png" alt=""></div>
    </div>
</template>

<script>
  import myProgress from '../../components/Progress'

  export default {
    name: "DetailSupport",
    components: {
      'my-progress': myProgress
    },
    data() {
      return {
        changeGray: false,
        buttonText: '立即支持',
        swiperList: [],
        id: this.$queryParams(location.href).id,
        list: [],
        supportList: [],
        aimNum: '',
        nowNum: '',
        supportType: ''
      }
    },
    computed: {
      precent: function f() {
        if (this.nowNum / this.aimNum > 1) {
          return 100;
        } else {
          return this.nowNum / this.aimNum * 100;
        }
      }
    },
    created() {
      document.title = '应援详情';
    },
    mounted() {
      let params = this.$queryParams(location.href);
      this.getDetail(params);
      this.getSupportList(params);
      setTimeout(() => {
        this.deadline();
      }, 500)
    },
    methods: {
      deadline: function (t) {
        let date = new Date().getTime();
        if (this.list.islong === 1) {

        } else if (date > this.list.endtime) {
          this.buttonText = '查看其他应援';
          this.changeGray = true;
        }
      },
      getDetail: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/assistance/getActivityDetail', // 应援详情
          data: this.$qs.stringify({
            id: params.id,
            currentuserid: params.currentuserid
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              // this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.list = res.data.data;
              this.list.detailimgurl = this.list.detailimgurl.split(',');
              if (res.data.data.moneyneed === 0) {
                this.aimNum = res.data.data.integralneed;
                this.nowNum = res.data.data.integralnow;
                this.supportType = '积分';
              }
              if (res.data.data.integralneed === 0) {
                this.aimNum = res.data.data.moneyneed / 100;
                this.nowNum = res.data.data.moneynow / 100;
                this.supportType = '元';
              }
              if (res.data.data.endtime === 0) {
                this.list.endtime = '长期';
              }

            }
          }
        ).catch((err) => {
          console.log(err);
        })
      },
      getSupportList: function (params) {
        this.$axios({
          method: 'post',
          url: process.env.VUE_APP_BASE4 + '/assistance/singerActivityContributionListPage', // 贡献榜
          data: this.$qs.stringify({
            id: params.id,
            pageNum: 1,
            pageSize: 3
          })
        }).then((res) => {
            if (res.data.status !== 1000) {
              // this.$toast(res.data.msg);
              return false;
            } else if (res.data.data !== null) {
              this.supportList = res.data.data;
              for (let i = 0; i < this.supportList.list.length; i ++) {
                if (this.supportList.list[i].type === 0) {
                  this.supportList.list[i].contribution = (this.supportList.list[i].contribution / 100);
                }
              }
            }
          }
        ).catch((err) => {
          console.log(err);
        })
      }
    }
  }
</script>

<style scoped lang="scss">
    .img-box {
        padding-bottom: .3rem;
        font-size: 0;
        .showImg {
            width: 100%;
        }
    }

    .default-img {
        width: 100%;
    }

    .grayColor {
        background: #c7c1c1 !important;
    }

    .gray-font {
        border: .02rem solid gray !important;
        color: gray !important;
    }

    .top-img {
        display: block;
        width: 100%;
    }

    .detail-presell {
        height: 100%;
        width: 100%;
        .swiper {
            width: 100%;
        }
        .presell-title {
            color: #000;
            font-weight: 600;
            /*max-height: 1.1rem;*/
            line-height: .4rem;
            padding: .2rem;
            background: #fff;
            /*overflow: hidden;*/
            /*-ms-text-overflow: ellipsis;*/
            /*text-overflow: ellipsis;*/
            /*display: -webkit-box;*/
            /*-webkit-box-orient: vertical;*/
            /*-webkit-line-clamp: 2;*/
            .t {
                font-size: .28rem;
                vertical-align: middle;
                margin: 0 .05rem;
            }
            .star {
                padding: 0 .08rem;
                background: #FF2E4D;
                text-align: center;
                font-size: .26rem;
                color: #fff;
                -webkit-border-radius: .14rem;
                -moz-border-radius: .14rem;
                border-radius: .14rem;
                vertical-align: middle;
            }
        }
        .support-progress {
            width: 100%;
            height: .7rem;
            line-height: .3rem;
            padding: 0 .2rem .4rem .2rem;
            font-size: .24rem;
            color: #333333;
            background: #fff;
        }
        .van-p {
            width: 100%;
            padding: 0 2.5%;
            background: #fff;
            margin: 0 auto;
        }
        .support-time {
            height: 1rem;
            width: 100%;
            background: #fff;
            display: flex;
            justify-content: space-between;
            color: #909090;
            font-size: .24rem;
            padding: .7rem .2rem .4rem .2rem;
        }
        .blue-tips {
            background: #fff;
            padding: .03rem .23rem .3rem .2rem;
            font-size: .22rem;
            color: #458AFC;
        }
        .follow-sth {
            height: 1.37rem;
            width: 100%;
            padding: 0 .2rem;
            background: #fff;
            margin: .32rem 0;
            overflow: hidden;
            .follow-head {
                height: .71rem;
                width: .71rem;
                margin: .33rem 0;
                -webkit-border-radius: 50%;
                -moz-border-radius: 50%;
                border-radius: 50%;
                float: left;
            }
            .follow-nickname {
                height: 50%;
                line-height: 1rem;
                width: 5rem;
                float: left;
                font-size: .33rem;
                color: #333333;
                padding: 0 .15rem;
            }
            .fans-num {
                height: 50%;
                width: 5rem;
                line-height: .5rem;
                font-size: .22rem;
                color: #909090;
                padding: 0 .15rem;
                float: left;
            }
            .follow-btn {
                float: right;
                height: .56rem;
                width: 1rem;
                -webkit-border-radius: .1rem;
                -moz-border-radius: .1rem;
                border-radius: .1rem;
                border: .02rem solid #FF2E4D;
                color: #FF2E4D;
                line-height: .56rem;
                text-align: center;
                margin: .41rem 0;
            }
        }
        .should-know {
            color: #333333;
            font-size: .28rem;
            margin-bottom: .3rem;
        }
        .contribution-list {
            width: 100%;
            background: #fff;
            margin-bottom: 1rem;
            .No {
                height: 1.18rem;
                padding: 0 .3rem;

                &:nth-of-type(1) {
                    .contribution-No {
                        background: url("../../assets/images/yi.png") center no-repeat;
                        background-size: 100% 100%;
                    }
                }
                &:nth-of-type(2) {
                    .contribution-No {
                        background: url("../../assets/images/er.png") center no-repeat;
                        background-size: 100% 100%;
                    }
                }
                &:nth-of-type(3) {
                    .contribution-No {
                        background: url("../../assets/images/san.png") center no-repeat;
                        background-size: 100% 100%;
                    }
                }
                .contribution-No {
                    display: block;
                    height: .46rem;
                    width: .38rem;
                    margin: .36rem 0;
                    float: left;
                    text-align: center;
                    color: #fff;
                    line-height: .44rem;
                    font-size: .2rem;
                }
                .contribution-info {
                    height: 100%;
                    width: 91%;
                    float: right;
                    line-height: 1.18rem;
                    border-bottom: 1px solid #f2f2f2;
                    display: flex;
                    justify-content: space-between;
                    .contribution-head {
                        display: block;
                        height: .73rem;
                        width: .73rem;
                        margin: .225rem .225rem .225rem 0;
                        -webkit-border-radius: 50%;
                        -moz-border-radius: 50%;
                        border-radius: 50%;
                    }
                    .contribution-name {
                        flex: 1;
                        color: #333333;
                        font-size: .28rem;
                        font-weight: 600;
                        overflow: hidden;
                        -ms-text-overflow: ellipsis;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }
                    .contribution-value {
                        color: #FF2E4D;
                        font-size: .34rem;
                        font-weight: 600;
                        overflow: hidden;
                        -ms-text-overflow: ellipsis;
                        text-overflow: ellipsis;
                        white-space: nowrap;
                    }

                }
            }

        }
        .footer-btn {
            height: .98rem;
            width: 100%;
            background: #fff;
            margin: 0 auto;
            position: fixed;
            left: 0;
            bottom: 0;
            right: 0;
            border: 1px solid #F2F2F2;
            .opreate-btn {
                display: block;
                height: .7rem;
                border-radius: .35rem;
                line-height: .7rem;
                border: 0;
                font-size: .28rem;
                background: #FF2E4D;
                width: 5rem;
                margin: .14rem auto;
            }
        }
    }
</style>
